.banner-item{
	width: 100%;
	padding-top: 37.3%;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
}
.title-wrapper{
	font-size: 0;
	padding: 25px 0 18px;
	span{
		display: inline-block;
		vertical-align: middle;
	}
	&-icon{
		width: 25px;
		height: 25px;
		margin-bottom: 2px;
	}
	&-text{
		font-size: 20px;
		font-weight:500;
		padding-left: 10px;
	}
}
.video-item-wrapper{
	display: flex;
	flex-wrap: wrap;
	.video-item:nth-child(2n + 1) {
		padding-right: 3px;
	}
	.video-item:nth-child(2n) {
		padding-left: 3px;
	}
}
.video-item{
	width: 50%;
	box-sizing: border-box;
	margin-bottom: 14px;
	a{
		display: block;
		height: 100%;
		border-radius: 0 0 4px 4px;
		background: white;
	}
	&__top{
		padding-top: 55%;
		position: relative;
		border-radius: 4px 4px 0 0;
		background-image: url('../images/default-live.png');
		// transition: all 0.3s linear;
		&.big{
			padding-top: 100%;
		}
		&-cover{
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0;
			top: 0;
			z-index: 2;
			border-radius: 4px 4px 0 0;
			// transition: all 0.3s linear;
		}
		&-shade{
			width: 100%;
			height: 50%;
			position: absolute;
			left: 0;
			bottom: 0;
			background:linear-gradient(rgba(0,0,0,0) 0%,rgba(0,0,0,0.8) 100%);
			opacity:0.9179;
			z-index: 2;
		}
		&-bottom{
			width: 100%;
			position: absolute;
			bottom: 5px;
			left: 0;
			display: flex;
			font-size: 0;
			align-items: center;
			z-index: 4;
			span{
				display: block;
			}
			&__header{
				width: 15px;
				height: 15px;
				border-radius: 50%;
				margin-left: 5px;
				margin-right: 7px;
			}
			&__name{
				flex: 1;
				font-family:PingFangSC-Regular;
				font-weight:400;
				color:rgba(255,255,255,1);
				font-size: 12px;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
			}
			&__hint{
				margin: 0 6px;
				font-size: 11px;
				color: #EA5455;
				margin-top: 1px;
			}
			&__number{
				font-size:12px;
				font-family:PingFangSC-Regular;
				font-weight:400;
				color:rgba(255,255,255,1);
				margin-right: 10px;
			}
		}
	}
	&__bottom{
		// height: 34px;
		padding: 6px 10px;
		background: white;
		border-radius: 0 0 4px 4px;
		&-topic{
			font-size: 0;
			margin-bottom: 5px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			span{
				display: inline-block;
				vertical-align: middle;
				font-size: 13px;
				color: #FF7800;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				padding-top: 2px;
			}
		}
		&-name{
			color: #666;
			font-size: 12px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
	}
}
.footer-wrapper{
	padding: 27px 27px 40px;
	&__top{
		color: #333333;
		text-align: center;
		font-size: 0;
		span{
			display: inline-block;
			vertical-align: middle;
			font-size: 15px;
		}
		&-icon{
			width: 17px;
			height: 17px;
			background-image: url('../images/back.png');
			margin-right: 5px;
		}
		span:active{
			color: #666;
		}
	}
	&__button{
		margin-top: 30px;
		font-size: 15px !important;
		color: white !important;
	}
	&__copy{
		font-size: 12px;
		color: #999;
		text-align: center;
		line-height: 20px;
		margin-top: 20px;
	}
}

.none-recommand{
	width: 40%;
	display: block;
	margin: 30px auto 20px;
	position: relative;
}

.recommand-min-height{
	min-height: 400px;
}






